<template>
  <div class="Declare">
    <div style="width:100%;">
      <div class="itembox">
        <p class="detailtitle">代报关基础信息CCC</p>
        <div style="width:100%;min-height:200px;background:#f5f7f9;margin:15px 0" >
          <Row v-if="">
            <Col span="5">
              <ul class="detail_li detail1">
                <li class="itemli">
                  <span class="detail_title1">ID：</span>
                  <span>{{waybillinfo.ID}}</span>
                </li>
                <li class="itemli">
                  <span class="detail_title1">状态：</span>
                  <span>{{waybillinfo.ExcuteStatusDes}}</span>
                </li>
                <li class="itemli">
                  <span class="detail_title1">业务类型：</span>
                  <span>{{waybillinfo.SourceDes}}</span>
                </li>
                <li class="itemli" v-if="waybillinfo.Condition!=null">
                  <Icon type="md-alert" style="font-size: 22px;color: #da2828;" />
                  <Tag color="geekblue" v-if="waybillinfo.Condition.AgencyCheck==true">代检查</Tag>
                  <Tag color="purple" v-if="waybillinfo.Condition.AgencyPayment==true">代垫货款</Tag>
                  <Tag color="blue" v-if="waybillinfo.Condition.AgencyReceive==true">代收货款</Tag>
                  <Tag color="cyan" v-if="waybillinfo.Condition.ChangePackaging==true">代收货款</Tag>
                  <Tag color="green" v-if="waybillinfo.Condition.LableServices==true">标签服务</Tag>
                  <Tag color="gold" v-if="waybillinfo.Condition.PayForFreight==true">垫付运费</Tag>
                  <Tag color="orange" v-if="waybillinfo.Condition.Repackaging==true">重新包装</Tag>
                  <Tag color="volcano" v-if="waybillinfo.Condition.UnBoxed==true">拆箱验货</Tag>
                  <Tag color="red" v-if="waybillinfo.Condition.VacuumPackaging==true">真空包装</Tag>
                  <Tag color="magenta" v-if="waybillinfo.Condition.WaterproofPackaging==true">防水包装</Tag>
                  <Tag color="lime" v-if="waybillinfo.Condition.QualityInspection==true">质检</Tag>
                  <Tag color="yellow" v-if="waybillinfo.Condition.Unboxing==true">拆包装</Tag>
                </li>
              </ul>
            </Col>
            <Col span="5">
              <ul class="detail_li">
                <li class="itemli">
                  <span class="detail_title2">制单时间：</span>
                  <span>{{waybillinfo.CreateDate|showDate}}</span>
                </li>
                <li class="itemli">
                  <span class="detail_title2">供应商：</span>
                  <span>{{waybillinfo.Supplier}}</span>
                  <Tooltip content="一级" placement="right">
                    <img style="vertical-align: middle" src="../../assets/img/vip_1.png" alt />
                  </Tooltip>
                </li>
                <li class="itemli">
                  <span class="detail_title2">到货方式：</span>
                  <span>{{waybillinfo.TypeDes}}</span>
                  <Badge
                    :count="11"
                    :offset="[9,-12]"
                    class-name="demo-badge-alone"
                    v-if="historydetail.waybillLIst.length>0"
                  >
                    <Dropdown @on-click="showhistory">
                      <a href="javascript:void(0)">历史到货</a>
                      <DropdownMenu slot="list">
                        <DropdownItem
                          v-for="(item,index) in historydetail.waybillLIst"
                          :name="item"
                          :key="index"
                        >
                          <span>{{item}}</span>
                        </DropdownItem>
                      </DropdownMenu>
                    </Dropdown>
                  </Badge>
                </li>
                <li class="itemli" v-if="waybillinfo.Type==1">
                  <span class="detail_title2">自提时间：</span>
                  <span>2020-3-26</span>
                </li>
                <li class="itemli" v-if="waybillinfo.Type==2">
                  <span class="detail_title2">预计到货时间：</span>
                  <span>2020-3-28</span>
                </li>
                <li class="itemli" v-if="waybillinfo.Type==1">
                  <span class="detail_title2">提货文件：</span>
                  <span
                    v-for="(item,index) in  waybillinfo.Files"
                    v-if="item.Type==10"
                  >{{item.CustomName}}</span>
                </li>
              </ul>
            </Col>
            <Col span="6">
              <ul class="detail_li">
                <li class="itemli">
                  <span class="detail_title3">入仓号：</span>
                  <span>{{waybillinfo.EnterCode}}({{waybillinfo.ClientName}})</span>
                </li>
                <li class="itemli">
                  <span class="detail_title3">运单号(本次)：</span>
                  <span>
                    <Input style="width:60%" v-model="waybillinfo.Code" />
                  </span>
                </li>
                <li class="itemli">
                  <span class="detail_title3">承运商(本次):</span>
                  <span>
                    <Select
                      v-model="waybillinfo.CarrierID"
                      :label-in-value="true"
                      filterable
                      allow-create
                      style="width:60%"
                    >
                      <Option
                        v-for="item in CarrierList"
                        :value="item.ID"
                        :key="item.ID"
                      >{{ item.Name }}</Option>
                    </Select>
                  </span>
                </li>
                <li class="itemli">
                  <span class="detail_title3">司&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;机:</span>
                  <span>
                    <Select
                      transfer
                      v-model="waybillinfo.Driver"
                      style="width:60%"
                      :label-in-value="true"
                      filterable
                    >
                      <Option
                        v-for="(item,index) in DriversArr"
                        :value="item.ID"
                        :key="index"
                      >{{ item.Name }}</Option>
                    </Select>
                  </span>
                </li>
                <li class="itemli">
                  <span class="detail_title3">车&nbsp;&nbsp;牌&nbsp;&nbsp;号:</span>
                  <span>
                    <Select
                      transfer
                      v-model="waybillinfo.CarNumber1"
                      style="width:60%"
                      :label-in-value="true"
                      @on-create="handleCreate3"
                    >
                      <Option
                        v-for="item in CarArr"
                        :value="item.ID"
                        :key="item.ID"
                      >{{ item.CarNumber1 }}&nbsp;&nbsp;&nbsp;&nbsp;{{ item.CarNumber2 }}</Option>
                    </Select>
                  </span>
                </li>
                <li class="itemli">
                  <span class="detail_title3">输送地：</span>
                  <span>
                    <span
                      v-if="waybillinfo.ConsignorPlaceText!=''"
                    >{{waybillinfo.ConsignorPlaceText}}</span>
                    <span v-else style="color:red;">暂无输送地</span>
                    <Icon
                      class="sethover"
                      @click="clickClient(waybillinfo.ConsignorPlace,'ClientCode','ClientCode')"
                      type="md-create"
                    />
                  </span>
                </li>
              </ul>
            </Col>
            <Col span="5">
              <ul class="detail_li" style="margin-left:20px;">
                <li class="itemli">
                  <span class>订单号：</span>
                  <span>{{waybillinfo.OrderID}}</span>
                </li>
                <li class="itemli">
                  <div class="setupload">
                    <Button
                      type="primary"
                      size="small"
                      icon="ios-cloud-upload"
                      @click="SeletUpload(waybillinfo.WaybillID)"
                    >传照</Button>
                  </div>
                  <div class="setupload">
                    <Button
                      size="small"
                      type="primary"
                      icon="md-reverse-camera"
                      @click="fromphotos('Waybill')"
                    >拍照</Button>
                  </div>
                </li>
                <li style="clear: both;">
                  <div v-for="item in waybillinfo.Files" v-if="item.Type==8000">
                    <span>{{item.CustomName}}</span>
                    <Icon type="ios-trash-outline" @click.native="handleRemove(item)"></Icon>
                  </div>
                </li>
              </ul>
            </Col>
            <Col span="3">
              <div
                style="position: absolute; top: 147px; right: 0;"
                v-if="waybillinfo.WaybillType==1&&waybillinfo.ExcuteStatus!=105"
              >
                <Button type="primary" @click="TakeGoods">{{TakeGoodsName}}</Button>
              </div>
            </Col>
          </Row>
        </div>
      </div>
      <div class="itembox">
        <p class="detailtitle">产品清单</p>
        <div style="margin:15px 0">
          <ButtonGroup style="width:28%">
            <Input
              v-model="searchkey"
              placeholder="请输入品牌或型号"
              clearable
              @on-clear="search_pro"
              style="width:80%;float:left;position: relative;left: 3px"
            />
            <Button style="float:left" @click="search_pro" type="primary">筛选</Button>
          </ButtonGroup>
          <!-- <Button type="primary" @click="detailelist">清单打印</Button>
          <Button type="primary" @click="Labelprint">标签打印</Button> -->
          <Button
            type="primary"
            @click="clickshowchangebox"
            :disabled="waybillinfo.ExcuteStatus== 120 ? true:false"
          >一键入箱</Button>
          <Button type="primary" @click="showBudget('meet','in',waybillinfo.Source)">收入</Button>
          <Button type="primary" @click="showBudget('meet','out',waybillinfo.Source)">支出</Button>
          <Button type="primary" @click="abnormal_enter=true">无通知产品录入</Button>
          <Button type="primary" @click="testrouter">跳转</Button>
          <div style="float:right">
            <Button
              :disabled="waybillinfo.ExcuteStatus== 120 ? true:false"
              type="primary"
              shape="circle"
              icon="md-checkmark"
              @click="finish_btn"
            >入库完成</Button>
            <Button
              :disabled="waybillinfo.ExcuteStatus== 120 ? true:false"
              type="warning"
              shape="circle"
              icon="ios-alert-outline"
              @click="isAbnormalclick"
            >到货异常</Button>
          </div>
        </div>
        <div>
          <div class="detail_tablebox">
             <Table
                ref="selection"
                :columns="tabletitle"
                :data="ProductList"
                @on-selection-change="handleSelectRow"
              >
              <template slot-scope="{ row, index }" slot="indexs">{{index+1}}</template>
              <template slot-scope="{ row, index }" slot="PartNumber">
                <div style="display: flex;justify-content: space-between;align-items: center;">
                  <div style="float:left;text-align:left">
                    <span style="width:120px;overflow: hidden;">{{row.Product.PartNumber}}</span>
                  </div>
                  <ul style="float:right;">
                    <li>
                      <Tag v-if="row.Conditions.IsCIQ==true" color="primary">商检</Tag>
                    </li>
                    <li>
                      <Tag v-if="row.Conditions.IsCCC==true" color="warning">CCC</Tag>
                    </li>
                    <li>
                      <Tag v-if="row.Conditions.IsEmbargo==true" color="error">禁运</Tag>
                    </li>
                    <li>
                      <Tag v-if="row.Conditions.IsHighPrice==true" color="magenta">高价值</Tag>
                    </li>
                  </ul>
                </div>
              </template>
              <template slot-scope="{ row, index }" slot="Arrival">
                <span>{{row.Quantity}}</span>
              </template>
              <template slot-scope="{ row, index }" slot="Already">
                <span>{{row.ArrivedQuantity}}</span>
              </template>
              <template slot-scope="{ row, index }" slot="Surplus">
                <P v-if="row.isenter!=true">
                  <span v-if="Number(row.LeftQuantity)<=0">0</span>
                  <span v-else>{{row.LeftQuantity}}</span>
                </P>
              </template>
              <template slot-scope="{ row, index }" slot="Country_origin">
                <span>{{row.origintext}}</span>
                <Icon class="sethover" v-if="row._disabled==false" @click="clickClient(row.origin,row,index)" type="md-create" />
              </template>
              <template slot-scope="{ row, index }" slot="TinyOrderID">
                  {{row.TinyOrderID}}
              </template>
              <template slot-scope="{row,index}" slot="Boxing_code">
                <div style="display:inline-block;width:80%">
                  <Select
                    v-model="row.BoxCode"
                    :disabled='row._disabled==false?false:true'
                    :transfer="true"
                    filterable 
                    allow-create
                    @on-create="handleCreate1($event,index)"
                    @on-change="clickDropdown($event,index)"
                  >
                    <Option
                      v-for="(item,index) in boxingarr"
                      :value="item.Code"
                      :key="index"
                      :label="item.Code"
                    >
                      <span>{{item.Code}}</span>
                      <span style="color:#ccc;margin-left: 8px;">0</span>
                    </Option>
                  </Select>
                </div>
              </template>
              <template slot-scope="{ row, index }" slot="imglist">
                <p v-for="item in row.Files">
                  <span v-if="item.Type==8000">{{item.CustomName}}</span>
                  <Icon
                    type="ios-trash-outline"
                    :ref="row.ID"
                    @click.native="handleRemovelist(row,item)"
                  ></Icon>
                </p>
              </template>
              <template slot-scope="{ row, index }" slot="operation">
                <div class="setupload">
                  <Button
                   :disabled='row._disabled==false?false:true'
                    type="primary"
                    size="small"
                    icon="ios-cloud-upload"
                    @click="SeletUpload(row.InputID)"
                  >传照</Button>
                </div>
                <div class="setupload">
                  <Button
                   :disabled='row._disabled==false?false:true'
                    type="primary"
                    icon="md-reverse-camera"
                    size="small"
                    @click="fromphotos(row.InputID)"
                  >拍照</Button>
                </div>
                <div class="setupload">
                  <Button
                   :disabled='row._disabled==false?false:true'
                    v-if="row.iscx!=false"
                    type="primary"
                    size="small"
                    icon="md-checkmark"
                    @click="chaixiang(index,row)"
                  >拆项</Button>
                  <Button
                    v-if="row.iscx==false"
                    :disabled='row._disabled==false?false:true'
                    type="primary"
                    size="small"
                    icon="ios-trash-outline"
                    @click="removechaixiang(index,row)"
                  >删除</Button>
                </div>
              </template>
            </Table> 
            <div class="successbtn" style="margin-top:20px;">
              <Button
                type="primary"
                icon="md-checkmark"
                @click="finish_btn"
              >入库完成</Button>
              <Button
                type="warning"
                icon="ios-alert-outline"
                @click="isAbnormalclick"
              >到货异常</Button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 收支明细 开始 -->
    <Modal
      v-model="Budgetdetail"
      width="55%"
      :closable="false"
      :mask-closable="false"
      :footer-hide="true"
    >
      <div style="clear: both; overflow: hidden;font-size:16px;padding-bottom:10px">
        <Icon type="ios-close-circle" style="float:right" @click="closeBudget" />
      </div>
      <div>
        <router-view></router-view>
      </div>
    </Modal>
    <!-- 收支明细结束 -->
    <!-- 收支明细结束 -->
  <!-- 输送地列表与更改 开始-->
    <Modal v-model="setClientCode" title="选地地区" @on-cancel="cancel">
      <Select v-model="ClientCode" filterable :label-in-value="true" @on-change="changClientCode">
        <Option
          v-for="(item,index) in Conveyingplace2"
          :value="item.CorPlace"
          :key="item.ID"
        >{{ item.Text }}</Option>
      </Select>
      <div slot="footer">
        <Button @click="cancel">取消</Button>
        <Button type="primary" @click="primaryClientCode">确定</Button>
      </div>
    </Modal>
    <!-- 输送地列表与更改 结束-->

    <!-- 申请箱号 开始  暂时没有这个需求-->
    <!-- <Modal
        v-model="Applybox"
        title="箱号申请"
        @on-visible-change="visiblechange"
        @on-ok="okapply"
        @on-cancel="cancel"
      >
      <div>
        <label>
          日期：
          <Select v-model="applyfrom.DateStr" style="width:80%">
            <Option v-for="(item,index) in applydata" :value="item" :key="index">{{ item }}</Option>
          </Select>
        </label>
      </div>
      <div style="margin-top:20px">
        <label>
          备注：
          <Input v-model="applyfrom.Summary" placeholder="请输入备注" style="width:80%" />
        </label>
      </div>
    </Modal> -->
    <!-- 申请箱号结束 -->

    <!-- 一键入箱 开始 -->
    <Modal
      v-model="showchangebox"
      title="选择箱号"
      @on-visible-change="changboxvisible"
      @on-ok="ok_changebox"
      @on-cancel="cancel"
    >
      <Select v-model="newboxcode">
        <Option
          v-for="(item,index) in boxingarr"
          :value="item.Code"
          :key="index"
          :label="item.Code"
        >
          <span>{{item.Code}}</span>
          <span style="float:right;color:#ccc;margin-left:8px;">0</span>
        </Option>
      </Select>
    </Modal>
    <!-- 一键入箱 结束 -->
    <!-- 未通知产品信息录入  开始-->
        <Modal
          v-model="abnormal_enter"
          title="无通知产品录入"
          width="90"
          ref="modal"
          @on-ok="ok_abnormal"
          @on-cancel="cancel"
          @on-visible-change="changeabnormal_enter"
        >
          <div>
            <div>
              <p class="detailtitle">
                无通知产品录入
                <span style="margin-left: 15px;color:red;">注意：仅能录入该运单下与通知型号不匹配的产品信息</span>
              </p>
              <div style="padding:15px 0px;">
                <Noting-Enter :key="timer" ref="NotingEntername"  :Storagesarr="Storages"  :Boxingarr='boxingarr' :status='2'></Noting-Enter>
              </div>
            </div>
          </div>
        </Modal>

   <!-- 未通知产品信息录入 结束 -->
    <!-- 确认入库 开始 -->
    <div v-if="WarehousingMsg==true">
      <Modal
        v-model="WarehousingMsg"
        title="分拣完成"
        @on-ok="ok_Warehousing(115)"
        @on-cancel="cancel_Warehousing"
      >
        <div v-if="Nomatching.length!=0">
          <h2>以下型号数量与通知应到数量不符，是否继续进行入库操作</h2>
          <ul>
            <li v-for="(item,index) in Nomatching" :key="index">
              <h2>{{item}}</h2>
            </li>
          </ul>
        </div>
        <div v-else>
          <span>是否确认分拣完成</span>
        </div>
      </Modal>
    </div>
    <!-- 确认入库 结束 -->
    <!-- 异常到货 开始-->
    <Modal v-model="isAbnormal" title="到货异常" @on-visible-change="changeerror">
      <div slot="close">
        <Icon type="md-close" color="rgb(33, 28, 28)" @click="closeerror" style="font-size:18px;" />
      </div>
      <span style="line-height:26px;">异常原因</span>
      <Input v-model="Summary" type="textarea" :rows="2" placeholder="备注" />
      <div slot="footer">
        <Button @click="closeerror">取消</Button>
        <Button type="primary" @click="Abnormal_btn">确定</Button>
      </div>
    </Modal>
    <!-- 异常到货 结束-->




    <!-- 历史到货 开始 -->
    <!-- <Drawer :closable="true" :width="70" :mask-closable="true" v-model="historydata">
      <Historys-dom :key="historydetail.times" ref="Historygoods"></Historys-dom>
    </Drawer> -->
    <!-- 历史到货 结束 -->
  </div>
</template>
<script>
import Historys from "@/Pages/Common/Historygoods";
import NotingEnter from "@/Pages/Cgenter/NotingEnter";
import {
  GetBoxesdates,
  GetBoxes,
  Noticedetail,
  Boxenter,
  getWayParter,
  GetInputID,
  sortingupload,
  History,
  Carriers
} from "../../api";
import {
  CgDetail,
  cgenter,
  GetSortingID,
  historyList,
  GetDriversCars,
  search_detail,
  TakeGoods,
  CgBoxesShow,
  CgEnterSeries
} from "../../api/CgApi";
import {
  TemplatePrint,
  GetPrinterDictionary,
  FilePrint,
  FormPhoto,
  SeletUploadFile
} from "@/js/browser.js";
let product_url = require("../../../static/pubilc.dev");
let lodash = require("lodash");
export default {
  name: "",
  components: {
    "Historys-dom": Historys,
    "Noting-Enter": NotingEnter
  },
  data() {
    return {
      abnormal_enter: false,
      Storages:[],
      timer:'',
      TakeGoodsName: "我去提货",
      historydata: false, //历史到货的抽屉
      historydetail: {
        //历史到货数据
        times: "", //时间，每次获取新的版本
        waybillLIst: [] //运单列表
      },
      WarehousingMsg: false,
      Nomatching: "", //数量不符合的型号
      Summary: '',//异常备注信息
      isAbnormal: false, //异常对话框
      searchkey: "", //筛选
      SelectRow: [], //多选
      applydata: [],
      applyfrom: {
        WarehouseID: sessionStorage.getItem("UserWareHouse"), //库房id
        Summary: "", //备注
        CodePrefix: "WL", //前缀
        DateStr: "" //时间
      },
      detail_ID: "",
      CarrierList:[], //承运商列表
      CarArr: [], //车牌号
      DriversArr: [], // 司机,
      Conveyingplace: [], //输送地列表
      details: {
        //详情页
        wayBillID: "",
        total: 0,
        pageIndex: 1,
        pageSize: 1,
        WaybillNo: "90416165067", //运单号(本次)
        Carrier: "", //承运商(本次)
        PlaceDescription: "美国" //输送地,
      },
      waybillinfo: {}, //运单信息
      ProductList:[], //分拣列表
      wayBillID: this.$route.params.wayBillID, //运单号
      tabletitle: [
