    handleCreate1(val,index) {  //箱号添加
    console.log(this.boxingarr)
      // this.boxingarr.push({
      //   Code: val
      // });
       var reg = /^[0-9]*$/;
      if(reg.test(val)==true){
        var data={
          enterCode:this.waybillinfo.EnterCode,//必选参数，入仓号
          quantity:val,//
          date:Date.parse(new Date()),//必选参数，当前时间（可传空值过来会默认当前时间）
          adminID:sessionStorage.getItem("userID"),//装箱人
        }
        CgEnterSeries(data).then(res=>{
          if(res.success==true){
            this.boxingarr.push({
              Code: res.data
            });
            this.ProductList[index].BoxCode=res.data
          }
        })
       }else{
         this.ProductList[index].BoxCode=''
       }
    },
    
    handleCreate2(val) {
      //添加司机
      this.DriversArr.push({
        ID: val,
        Name: val
      });
    },
    handleCreate3(val) {
      this.CarArr.push({
        ID: val,
        Name: val
      });
    },
    changePartNumber(event, rowid, index, row) {
      //改变型号
      if (this.ProductList[index].InputID == rowid) {
        this.ProductList[index].Product.PartNumber = event.target.value;
        this.clicktest(this.ProductList[index]);
        for (var i = 0; i < this.ProductList.length; i++) {
          if (this.ProductList[i].PID == row.ID) {
            this.ProductList[i].Product.PartNumber = event.target.value;
          }
        }
      }
      // console.log(this.waybillinfo.Notices[index].Product);
      // this.waybillinfo.Notices[index]
    },
    trim(str) {
      //去除前后空格
      return str.replace(/(^\s*)|(\s*$)/g, "");
    },
    handleRemovelist(row, file) {
      var arr = this.waybillinfo.Notices;
      for (var j = 0; j < arr.length; j++) {
        //删除指定下标 的元素
        if (arr[j].ID == row.ID) {
          arr[j].Files.splice(file, 1);
        }
      }
    },
    handleRemove(file) {
      this.waybillinfo.Files.splice(this.waybillinfo.Files.indexOf(file), 1);
    },
