      //获取可用箱号
      var houseid = sessionStorage.getItem("UserWareHouse");
      var data = {
        whCode: sessionStorage.getItem("UserWareHouse"), //库房标识（库房编号）
        enterCode: this.waybillinfo.EnterCode, //入仓号
        date: "" //箱号日期（可为空，为空时展示当前日期的箱号）
      };
      CgBoxesShow(data).then(res => {
        console.log(res);
        if (res.length > 0) {
          this.boxingarr = res;
        }else{
          this.boxingarr=[]
        }
      });
