      if (imgdata.SessionID == this.waybillinfo.ID) {
        this.waybillinfo.Files.push(newfile);
      } else {
        // alert(JSON.stringify(imgdata))
        // this.details.detailitem[imgdata.SessionID].Files.push(newfile);
        for (var i = 0; i < this.ProductList.length; i++) {
          if (this.ProductList.InputID == imgdata.SessionID) {
            this.ProductList.Files.push(newfile);
          }
        }
      }
